.card-container {
  margin-bottom: 20px;
  font-family: 'Oxygen', 'Helvetica', sans-serif;

  .title {
    margin: 0;
  }

  p.value {
    margin-bottom: 0;
    font-weight: 700;
    color: #454545;
  }

  p {
    margin: 0;
  }

  .card-header {
    min-height: 53px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .card-body {
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    min-height: 86px;
    div.card-title {
      display: flex;
      justify-content: center;
      align-items: center;
      .extra.positive {
        color: rgb(54, 186, 92);
      }
      .extra.negative {
        color: rgb(255, 44, 44);
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .card-container {
    .title {
      font-size: 1.15rem;
      font-weight: 500;
    }

    .title.cured {
      font-size: 1.1rem;
      font-weight: 500;
    }
    .value {
      font-size: 2.4rem;
    }
    div.card-title {
      flex-direction: column;
      margin-bottom: 0;
    }
  }
}

@media only screen and (min-width: 769px) {
  .card-container {
    .title {
      font-size: 24px;
    }
    p.value {
      font-size: 2.5rem;
      margin-right: 5px;
    }
  }
  div.card-title {
    flex-direction: row;
    .extra {
      margin-top: 9px;
    }
  }
}
